import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
// import Paper from '@material-ui/core/Paper'
// import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'
// import MenuItem from '@material-ui/core/MenuItem'

import Select from 'react-select'
import CreatableSelect from 'react-select/lib/Creatable'
import { makeStyles } from '@material-ui/core/styles'

import TagChip from '../tags/TagChip'
import style from './TagControl.style'
const useStyles = makeStyles(style)

/**
 * Just to be clear: the state here is being stored as []
 */
const TagControl = ({
  tags, onChange,
  hint, tagConfig, className
}) => {
  const classes = useStyles()
  // console.log('TagControl Rnder', { tags, tagConfig })
  const onSelectChange = (inputValue, { action, removedValue, option }) => {
    onChange(inputValue.map(iv => ({
      typeId: tagConfig.id,
      label: iv.label,
      xyCoords: null
    })))
  }

  const MultiValue = ({ removeProps, innerProps, data, ...props }) => {
    // console.log('MultiValue PROPS', { removeProps, innerProps, data, props })
    return (
      <TagChip tag={data} onDelete={(e) => {
        // console.log('EVENT', e)
        removeProps.onClick()
      }} color={tagConfig.color} edit />
    )
  }
  MultiValue.propTypes = {
    removeProps: PropTypes.any,
    innerProps: PropTypes.any,
    data: PropTypes.any
  }

  // Filter to just this tag type
  const tagValues = tags.filter(tag => tag.typeId === tagConfig.id && !tag.coords)

  // Now format it as {label, value: label} for react-select
  const selectOptions = tagValues.map(val => ({ label: val.label, value: val.val }))

  // Filter out things we haven't chosen yet
  // Also transform the options into a label/value pair for react-select
  const remainingTags = tagConfig.values
    .filter(val => tagValues.map(t => t.label).indexOf(val) === -1)
    .map(val => ({ label: val, value: val }))

  console.log('TAG DEBUG', { tags, tagConfig, tagValues, selectOptions, remainingTags })

  return (
    <div className={classNames(classes.root, className)}>
      <Typography variant='overline' className={classes.fieldTitle}>
        {tagConfig.label}
      </Typography>
      {!tagConfig.allowAdditions && (
        <Select
          options={remainingTags}
          classes={classes}
          isFocused
          components={{
          }}
          deleteRemoves
          value={selectOptions}
          isClearable={false}
          onChange={onSelectChange}
          placeholder='Select Tags'
          isMulti
          isSearchable
        />
      ) }
      { tagConfig.allowAdditions && (
        <CreatableSelect
          options={remainingTags}
          classes={classes}
          isFocused
          deleteRemoves
          components={{
          }}
          value={selectOptions}
          isClearable={false}
          onChange={onSelectChange}
          placeholder='Select Tags'
          isMulti
          isSearchable
        />
      )}
      {hint && <Typography variant='caption' className={classes.hint}>{hint}</Typography>}
    </div>
  )
}

TagControl.propTypes = {
  className: PropTypes.string,
  hint: PropTypes.string,
  tags: PropTypes.array,
  onChange: PropTypes.func.isRequired,
  tagConfig: PropTypes.object
}

export default TagControl
