// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { getWholeSeriesUpdate, allSeriesUpdate, feedUpdate, userSeriesUpdate } from './updateQueries'
import { createSeries, newSeriesKeys, newPhotoKeys } from '../mutations'
import { photoSummaryKeys } from '../fragments'
import { pushMutationPromise, popMutationPromise } from '../offline'
import { allSeries, userSeries, getMyFeed } from '../queries'
import _ from 'lodash'

export const withMutation = graphql(createSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      createSeriesMutation: async (newSeries, newPhoto, ext) => {
        const { id, gravatar, gravatarProfile, username } = ownProps.user
        const meUserSummary = { id, gravatar, gravatarProfile, username, __typename: 'UserSummary' }
        if (!newSeries.access) newSeries.access = 'PRIVATE'

        const variables = {
          series: _.pick(newSeries, newSeriesKeys),
          photo: _.pick(newPhoto, newPhotoKeys),
          ext
        }
        const optimisticResponse = createOptimisticResponse({ meUserSummary, newSeries, newPhoto, ext })
        const mutationStackId = await pushMutationPromise({ variables, optimisticResponse }, createSeries)
        console.log('createSeriesMutation:: start', { newSeries, newPhoto, variables, optimisticResponse, ext })
        return mutate({
          variables,
          refetchQueries: [
            { query: allSeries, variables: { limit: 100 } },
            { query: userSeries },
            { query: getMyFeed }
          ],
          optimisticResponse,
          update: updateThunk({}, ownProps.doneCreatingSeries)
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('createSeriesMutation:: done', data)
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  console.log('createSeriesMutation::updateThunk', { data })
  getWholeSeriesUpdate(store, data.createSeries.series)
  userSeriesUpdate(store, data.createSeries.series)
  allSeriesUpdate(store, data.createSeries.series)
  feedUpdate(store, data.createSeries.series, data.createSeries.series.photos[0])

  if (cb) cb(data.createSeries.series.id)
}

const createOptimisticResponse = ({ meUserSummary, newSeries, newPhoto, ext }) => {
  const photoResponse = {
    id: newPhoto.id,
    meta: newPhoto.meta || null,
    seriesId: newSeries.id,
    createdOn: Date.now().toString(),
    note: newPhoto.note || null,
    createdUser: meUserSummary,
    updatedUser: meUserSummary,
    createdBy: meUserSummary.id,
    updatedBy: meUserSummary.id,
    uploadToken: newPhoto.uploadToken,
    updatedOn: Date.now().toString(),
    seriesCreatedBy: meUserSummary.id, // we can't answer this yet
    takenOn: newPhoto.takenOn,
    coords: newPhoto.coords || null,
    tags: newPhoto.tags || [],
    paths: {
      base: null,
      large: null,
      orig: null,
      thumb: null,
      medium: null,
      __typename: 'Paths'
    },
    __typename: 'Photo'
  }
  const photoResponseSummary = _.pick(photoResponse, photoSummaryKeys)

  return {
    createSeries: {
      series: {
        id: newSeries.id,
        name: newSeries.name,
        note: newSeries.note || null,
        access: newSeries.access,
        meta: newSeries.meta || null,
        featured: {
          ...photoResponseSummary,
          __typename: 'PhotoSummary'
        },
        latest: {
          ...photoResponseSummary,
          __typename: 'PhotoSummary'
        },
        seriesStatus: null,
        coords: newSeries.coords || null,
        direction: newSeries.direction || null,
        photoCount: 1,
        createdUser: meUserSummary,
        updatedUser: meUserSummary,
        createdBy: meUserSummary.id,
        updatedBy: meUserSummary.id,
        createdOn: Date.now().toString(),
        updatedOn: Date.now().toString(),
        watchers: 0,
        photos: [photoResponse],
        __typename: 'Series'
      },
      preAuth: null,
      __typename: 'NewSeries'
    }
  }
}

export default {
  withMutation,
  updateThunk
}
