import { getWholeSeries, allSeries, userSeries, getMyFeed, getUser } from '../queries'
import { seriesKeys, seriesSummaryKeys, photoSummaryKeys, seriesSummaryFragment } from '../fragments'
import gql from 'graphql-tag'

import _ from 'lodash'

/**********************************************************
 *
 * NOTE:
 *
 * The methods in this file are for modifying the Apollo cache
 * store. This ensures interoperability between queries and
 * helps us rewind and replay mutations after an app reload
 *
 *
 **********************************************************/

/**
 * Update a series in the cached getWholeSeries queries
 *
 * @param {*} store
 * @param {*} photo
 * @param {*} remove
 */
export const getWholeSeriesUpdate = (store, series) => {
  const variables = { id: series.id }
  const query = getWholeSeries
  let newData = {
    series: {}
  }
  try {
    const qVal = store.readQuery({ query, variables })
    newData = _.cloneDeep(qVal)
  }
  catch (e) {
    console.warn('updateQueries::getWholeSeriesUpdate readQuery failed', { newData, series })
    console.warn('updateQueries::getWholeSeriesUpdate', e)
  }
  try {
    newData.series = { ...newData.series, ...series }
    store.writeQuery({ query, variables, data: newData })
    console.log('updateQueries::getWholeSeriesUpdate DONE')
  }
  catch (e) {
    console.error('updateQueries::getWholeSeriesUpdate writeQuery failed', { newData, series })
    console.warerrorn('updateQueries::getWholeSeriesUpdate', e)
  }
}

/**
 * Add or remove a photo a full getWholeSeries cached query
 *
 * @param {*} store
 * @param {*} photo
 * @param {*} remove
 */
export const getWholeSeriesPhotoUpdate = (store, photo, remove = false) => {
  const variables = { id: photo.seriesId }
  const query = getWholeSeries
  try {
    // This may or may not exist yet
    const qVal = store.readQuery({ query, variables })
    const newData = _.cloneDeep(qVal)
    if (remove) {
      newData.series.photos = newData.series.photos.filter(ph => ph.id !== photo.id)
    }
    else {
      const idx = newData.series.photos.findIndex(ph => ph.id === photo.id)
      if (idx > -1) {
        newData.series.photos = [...newData.series.photos]
        newData.series.photos[idx] = photo
      }
      else {
        newData.series.photos = [...newData.series.photos, photo]
      }
    }
    store.writeQuery({ query, variables, data: newData })
    console.log('updateQueries::getWholeSeriesPhotoUpdate DONE')
  }
  catch (e) {
    console.error('updateQueries::getWholeSeriesPhotoUpdate WriteQuery failed', { photo, remove })
    console.error('updateQueries::getWholeSeriesPhotoUpdate', e)
  }
}

/**
 * HELPER Function. Help us add and remove one item from
 * any of the allSeries arrays in the cache
 *
 * @param {*} origArr
 * @param {*} series
 * @param {*} remove
 */
function updateSeriesSummaries (origArr, series, remove = false) {
  let newArr = []
  if (remove) {
    newArr = origArr.filter(s => s.id !== series.id)
  }
  else {
    const seriesIndex = origArr.findIndex(s => s.id === series.id)
    // UPDATE case
    const newAllSeriesEntry = {
      ..._.pick(series, seriesKeys),
      seriesStatus: null,
      __typename: 'SeriesSummary'
    }
    if (seriesIndex > -1) {
      newArr = origArr.map(s => s.id === series.id ? newAllSeriesEntry : s)
    }
    // INSERT case
    else {
      newArr = [...newArr, newAllSeriesEntry]
    }
  }
  console.log('updateQueries::updateSeriesSummaries HELPER', { origArr, series, newArr, remove })
  return newArr
}

/**
 * HELPER Function. Help us add and remove one feedItem from the cached array
 *
 * @param {*} origArr
 * @param {*} series
 * @param {*} photo
 * @param {*} remove
 */
function updateFeedList (origArr, series, photo, remove = false) {
  let newArr = []
  if (remove) {
    // remove the item
    newArr = origArr.filter(fItem => {
      if (series && fItem.series.id === series.id) return false
      else if (photo && fItem.photo.id === photo.id) return false
      else return true
    })
  }
  else {
    // Maybe this is just an update
    const idx = origArr.findIndex(fitem => fitem._id === photo.id)

    const newSeriesSummary = _.pick(series, seriesSummaryKeys)
    newSeriesSummary.__typename = 'SeriesSummary'

    const newPhotoSummary = _.pick(photo, photoSummaryKeys)
    newPhotoSummary.__typename = 'PhotoSummary'

    // Update the item
    if (idx > -1) {
      newArr = [...origArr]
      newArr[idx].photo = newPhotoSummary
    }
    // Insert a new item
    else {
      newArr = [...origArr, {
        _id: newPhotoSummary.id,
        series: newSeriesSummary,
        photo: newPhotoSummary,
        relation: 'OWNED',
        __typename: 'MyFeedResultItem'
      }]
    }
  }
  console.log('updateQueries::updateFeedList HELPER', { origArr, series, photo, newArr })
  return newArr
}

/**
 * Update a changed Series in the allSeries cache
 *
 * @param {*} store
 * @param {*} series
 * @param {*} remove
 */
export const allSeriesUpdate = (store, series, remove = false) => {
  const variables = { limit: 100 }
  const query = allSeries
  let newData = {
    allSeries: {
      seriesSummaries: [],
      nextToken: null,
      __typename: 'PaginatedSeries'
    }
  }
  try {
    // This may or may not exist yet
    const qVal = store.readQuery({ query, variables })
    newData = _.cloneDeep(qVal)
  }
  catch (e) {
    console.warn('updateQueries::allSeriesUpdate readQuery failed', { series, remove, newData })
    console.warn('updateQueries::allSeriesUpdate', e)
    if (series) {
      // if it fails we need to create the entry by hand
      const newSeriesSummary = _.pick(series, seriesSummaryKeys)
      newSeriesSummary.__typename = 'SeriesSummary'
      newData.allSeries.seriesSummaries = [newSeriesSummary]
    }
    else return
  }

  try {
    newData.allSeries.seriesSummaries = updateSeriesSummaries(newData.allSeries.seriesSummaries, series, remove)
    store.writeQuery({ query, variables, data: newData })
    console.log('updateQueries::allSeriesUpdate DONE')
  }
  catch (e) {
    console.error('updateQueries::allSeriesUpdate writeQuery failed', { series, remove, newData })
    console.error('updateQueries::allSeriesUpdate', e)
  }
}

/**
 * Add or remove a photot to the allSeries cached query
 *
 * @param {*} store
 * @param {*} series
 * @param {*} photo
 * @param {*} remove
 */
export const allSeriesPhotoUpdate = (store, series, photo, remove = false) => {
  const query = allSeries
  let newData = {
    allSeries: {
      seriesSummaries: [

      ],
      nextToken: null,
      __typename: 'PaginatedSeries'
    }
  }
  try {
    const qVal = store.readQuery({ query, variables: { limit: 100 } })
    newData = _.cloneDeep(qVal)
  }
  catch (e) {
    console.warn('updateQueries::allSeriesPhotoUpdate:: allSeries readQuery failed', { series, remove, photo, newData })
    console.warn('updateQueries::allSeriesPhotoUpdate', e)
    if (series) {
      // if it fails we need to create the entry by hand
      const newSeriesSummary = _.pick(series, seriesSummaryKeys)
      newSeriesSummary.__typename = 'SeriesSummary'
      newData.allSeries.seriesSummaries = [newSeriesSummary]
    }
    else return
  }

  try {
    newData.allSeries.seriesSummaries = newData.allSeries.seriesSummaries.map(s => {
      if (s.id !== photo.seriesId) return s
      else {
        const newS = _.cloneDeep(s)
        // The DELETE case
        const photoIndex = series.photos.findIndex(ph => ph.id === photo.id)
        if (remove && series) {
          // set a new latest photo
          if (newS.latest.id === photo.id) {
            newS.latest = series.photos[series.photos.length - 1]
          }
          // set a new featured photo
          if (newS.featured.id === photo.id) {
            newS.featured = series.photos[series.photos.length - 1]
          }
          newS.photoCount -= 1
        }
        // the INSERT case
        else if (photoIndex === -1) {
          const photoSummary = _.pick(photo, photoSummaryKeys)
          photoSummary.__typename = 'PhotoSummary'
          newS.latest = photoSummary
          newS.photoCount += 1
        }
        // The UPDATE case
        else {

        }
        return newS
      }
    })
    store.writeQuery({ query, data: newData })
    console.log('updateQueries::allSeriesPhotoUpdate DONE')
  }
  catch (e) {
    console.error('updateQueries::createPhotoMutation:: allSeries read or write ERROR', { series, photo, remove })
    console.error('updateQueries::createPhotoMutation', e)
  }
}

