import { endpoints } from '../config'
/**
 * NOTEL this is mirrored on the server. MAKE SURE YOU UPDATE IT IN BOTH PLACES
 */
export const createShareUrl = (series, photoId, noRoot = false) => {
  if (!series) return null
  const obj = { type: 'series', s: series.id, p: photoId }
  const encodedId = encodeURI(JSON.stringify(obj))
  const url = `${window.location.origin}${process.env.PUBLIC_URL}/?fromqr=${encodedId}`

  const ogShare = `${endpoints.share}/${series.id}/${photoId || ''}`
  // console.log('SOCIAL URL', { url, ogShare })
  return { url, ogShare }
}

export const decodeShareUrl = (search) => {
  try {
    const find = '?fromqr='
    const foundIdx = search.indexOf(find)
    if (foundIdx < 0) return null
    const newData = search.slice(foundIdx + find.length)
    const decodedUrl = JSON.parse(decodeURI(newData))
    return decodedUrl
  }
  catch (err) {
    console.error(err)
  }
  return null
}
