import React from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import FormLabel from '@material-ui/core/FormLabel'
import { makeStyles } from '@material-ui/core/styles'

const style = theme => ({
  root: {
    position: 'relative',
    display: 'block',
    overflow: 'hidden',
    whiteSpace: 'pre-line',
    '&::before': {
      content: '"  "',
      display: 'block',
      position: 'absolute',
      bottom: 0,
      width: '100%',
      height: 100,
      background: `linear-gradient(180deg, rgba(255,255,255,0) 0%, ${theme.palette.background.paper} 100%)`
    },
    placeholder: {
      color: theme.palette.text.secondary
    }
  }
})
const useStyles = makeStyles(style)

const ShortLongText = ({ label, text, height, onClick, placeholder }) => {
  const classes = useStyles()
  return (
    <div onClick={onClick}>
      {label && (
        <FormLabel className={classes.formLabel}>
          {label}
        </FormLabel>
      )}
      {!text && (
        <Typography
          component='div'
          variant='caption'
          color='inherit'
          style={{ height }}
          className={classNames(classes.placeholder)}
        >
          {placeholder || 'none'}
        </Typography>
      )}
      {text &&
      <Typography
        component='div'
        variant='body1'
        color='inherit'
        style={{ height }}
        className={classNames(classes.root)}
      >
        {text}
      </Typography>
      }
    </div>
  )
}

ShortLongText.propTypes = {
  label: PropTypes.string,
  text: PropTypes.string,
  placeholder: PropTypes.string,
  height: PropTypes.number,
  onClick: PropTypes.func
}
ShortLongText.defaultProps = {
  height: 100
}

export default ShortLongText
