import SeriesConfig from './SeriesConfig'

class Tags {
  constructor (seriesConfig, tags) {
    this.tags = tags
    this.seriesConfig = new SeriesConfig(seriesConfig)
  }

  findTagByLabel = (typeId, tagLabel) => {
    return this.seriesConfig.getTagType(typeId).values
      .find(tt => tt.label === tagLabel)
  }

  // Filter the list of tags to those that are XY tags
  getXYTags = () => {
    const xyTagLabels = this.seriesConfig.getXYTagTypes().map(tt => tt.label)
    return this.tags.filter(tag => xyTagLabels.indexOf(tag.typeId) > -1 && tag.coords && tag.coords.x)
  }

  getNonXYTags = () => {
    const NonXYTagLabels = this.seriesConfig.getNonXYTagTypes().map(tt => tt.label)
    return this.tags.filter(tag => NonXYTagLabels.indexOf(tag.typeId) > -1)
  }

  createNewTagObj = (label, typeId) => (
    { label, typeId }
  )

  createExistingTagObj = (label, typeId) => ({
    ...this.findTagByLabel(typeId, label),
    typeId
  })

  createNewXYTagObj = (label, typeId, coords) => ({
    label,
    typeId,
    coords
  })

  createExistingXYTagObj = (label, typeId, coords) => ({
    ...this.findTagByLabel(typeId, label),
    typeId,
    coords
  })

  // Get tags in a way that's easy to render them
  // We augment these tags with relative positions
  // and a group number for coloring.
  getRelXYTags = (multiplier) => this.getXYTags().map(tag => ({
    ...tag,
    group: this.seriesConfig.getTagTypeIndex(tag.typeId),
    relCoords: {
      x: tag.coords.x * multiplier,
      y: tag.coords.y * multiplier
    }
  }))
}

export default Tags
