import React, { useState, useLayoutEffect } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import QRCode from 'qrcode'
import { createShareUrl } from '../../../lib/share'
import { makeStyles } from '@material-ui/core/styles'
import { Paper, Link } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
  root: {
  },
  title: {
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText
  },
  qrPaper: {
    textAlign: 'center',
    padding: theme.spacing(0, 2)
  },
  qrCode: {
    width: '100%',
    minHeight: 300,
    maxHeight: 400
  }
}))

const QRDisplay = ({ onClose, open, series, photoId }) => {
  const classes = useStyles()
  const [qrCodeEl, setQRCodeEl] = useState(null)

  const { url, ogShare } = createShareUrl(series, photoId)
  const canShare = ['OPEN', 'PUBLIC'].indexOf(series.access) > -1
  useLayoutEffect(() => {
    QRCode.toString(url, (err, svgStr) => {
      if (err) console.error(err)
      else setQRCodeEl(svgStr)
    })
  }, [series])

  useLayoutEffect(() => {
    if (navigator.share && open && canShare) {
      let text = 'MorPhoto Series'
      if (series.note) text = series.note.length > 100 ? series.note.slice(100) + '...' : series.note
      navigator.share({
        title: series.name,
        text,
        url: ogShare
      })
        .then(() => console.log('Successful share'))
        .catch((error) => console.log('Error sharing', error))
    }
  }, [open])

  return (
    <Dialog
      open={open}
      fullWidth

      onClose={onClose}
      className={classes.root
      }>
      <DialogTitle className={classes.title}>{series.name}</DialogTitle>
      <Paper className={classes.qrPaper}>
        {canShare
          ? <svg className={classes.qrCode} dangerouslySetInnerHTML={{ __html: qrCodeEl }} />
          : <Typography variant='' color='error'>You cannot share a private series.</Typography>
        }
        {canShare &&
          <Link href={url}>
          Share Link
          </Link>
        }
        {series.coords && (
          <Typography component='div' className={classes.coords} variant='overline'>
          coords: [ {series.coords[0]}, {series.coords[1]} ]
          </Typography>
        )}
      </Paper>
    </Dialog>
  )
}

QRDisplay.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  series: PropTypes.object.isRequired,
  photoId: PropTypes.string
}

export default QRDisplay
