import React, { useContext, useState } from 'react'
import PropTypes from 'prop-types'
// import classNames from 'classnames'
import CssBaseline from '@material-ui/core/CssBaseline'
import Typography from '@material-ui/core/Typography'
// import Snackbar from '@material-ui/core/Snackbar'
// import SnackbarContent from '@material-ui/core/SnackbarContent'
import AppBar from '@material-ui/core/AppBar'
import List from '@material-ui/core/List'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import IconButton from '@material-ui/core/IconButton'

import ArrowBackIcon from '@material-ui/icons/ArrowBack'
// import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline'
// import ErrorIcon from '@material-ui/icons/Error'
// import LockIcon from '@material-ui/icons/Lock'
import SettingsBackupRestore from '@material-ui/icons/SettingsBackupRestore'
import WarningIcon from '@material-ui/icons/Warning'
import DeleteDialog from './PhotoSeries/views/dialogs/DeleteDialog'
import Toolbar from '@material-ui/core/Toolbar'
import { AppContext } from '../contexts'

import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
  root: {

  },
  nuclear: {
    color: 'red'
  }
}))

const AppSettings = ({ onMenuBack }) => {
  const classes = useStyles()
  const { resetAllState, resetApp } = useContext(AppContext)
  const [deleteOpen, setDeleteOpen] = useState()
  return (
    <div className={classes.root}>
      <CssBaseline /> {/* You only need this in a root element. Don't put it anywhere else in the dom */}
      <AppBar position='static' className={classes.appBar}>
        <Toolbar disableGutters>
          <IconButton className={classes.menuButton} color='inherit' onClick={onMenuBack} aria-label='Menu'>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant='subtitle1' color='inherit' className={classes.toolbarTitle}>
            Offline data manager
          </Typography>
        </Toolbar>
      </AppBar>
      <div className={classes.mapWrapper}>
        <List>
          <ListItem button onClick={() => setDeleteOpen({
            action: resetAllState,
            title: 'Reset App State',
            message: 'Might resolve a temporary glitch. Unfortunately this will wipe your upload queue.'
          })}>
            <ListItemIcon>
              <SettingsBackupRestore />
            </ListItemIcon>
            <ListItemText
              primary={'Reset app state'}
              secondary={'Might resolve a temporary glitch.'}
            />
          </ListItem>
          <ListItem button className={classes.nuclear} onClick={() => setDeleteOpen({
            action: resetApp,
            title: 'RESET ALL LOCAL DATA',
            message: 'All local images and data will be reset and you will be logged out. BE VERY SURE ABOUT THIS'
          })}>
            <ListItemIcon color='inherit'>
              <WarningIcon color='inherit' />
            </ListItemIcon>
            <ListItemText
              primary={'RESET ALL LOCAL DATA'}
              secondary={'All local images and data will be reset and you will be logged out'}
            />
          </ListItem>
        </List>
      </div>
      {deleteOpen && (
        <DeleteDialog
          open={!!deleteOpen}
          title={deleteOpen && deleteOpen.title}
          message={deleteOpen && deleteOpen.message}
          onConfirm={confirmed => {
            if (confirmed) deleteOpen.action()
            setDeleteOpen(null)
          }}
        />
      )}
    </div>
  )
}

AppSettings.propTypes = {
  onMenuBack: PropTypes.func.isRequired
}

export default AppSettings
