import React, { useEffect } from 'react'
import { Query, ApolloProvider, compose } from 'react-apollo'
import { IntlWrapper } from '../translations'
import { getUser } from '../graphql/queries'
import themes from '../themes'
import { AppContext } from '../contexts'
import Loading from '../components/auth/Loading'
import ErrorBoundary from '../lib/ErrorBoundary'
import ErrorConnect from './Error.connect'
import useGraphQL, { APOLLOSTATES } from '../graphql/useGraphQL'
import useAuthentication from '../lib/useAuthentication'
import useShowAddToHome from '../lib/useShowAddToHome'
import useOnline from '../lib/useOnline'
import useResponsive from '../lib/useResponsive'
import { decodeShareUrl } from '../lib/share'
import withReduxProvider from '../redux/withReduxProvider'
import { connect } from 'react-redux'
import actions from '../redux/actions'
import { imgStore, stateStore } from '../graphql/offline'
import { home as homeConfig } from '../config'
import { isIos, isPhone } from '../lib/sensors'

import App from '../components/App'
import { ThemeProvider } from '@material-ui/styles'

/**
 * HOC for AppContext and ApolloProvider
 */
export const withApolloAuthProvider = Wrapped => ({
  onQRClick, onAvatarClick, resetAllState, setUseOffline, onOfflineMenuClick, dismissInstall, onSettingsClick, flags, ...props
}) => {
  // console.log('withApolloAuthProvider', props)
  const { isGod, signedIn, authData, signOut, authScreen, forceSignIn } = useAuthentication(flags.useLoggedOut, setUseOffline)

  const { client: apolloClient, done: apolloDone, currState: apolloState } = useGraphQL()

  // This triggers all the PWA Goodness
  const { online, offlineSince } = useOnline()
  const { installPrompt, addToHomeScreen } = useShowAddToHome(flags.install, dismissInstall)
  const responsive = useResponsive()

  // If the user hasn't decided to be anonymous or sign in yet show them the login screen
  // console.log('Rendering AppConnect', { flags, responsive, props, signedIn, isGod, authData })
  useEffect(() => {
    if (signedIn === true && flags.useLoggedOut) setUseOffline(false)
    else if (signedIn === false && !flags.useLoggedOut) setUseOffline(true)
  }, [signedIn])

  // One time only query param parse
  useEffect(() => {
    try {
      const decodedObj = decodeShareUrl(window.location.search)

      if (decodedObj && decodedObj.type && decodedObj.s) {
        const { type, s, p } = decodedObj
        console.log('REDIRECTING', { type, s, p })
        switch (type) {
          case 'series':
            const queryVars = { activeSeries: s, initialPhoto: p }
            if (!signedIn) setUseOffline(true)
            props.setHomeState(homeConfig.AppStateTypes.SERIES, queryVars)
            break
          default:
            break
        }
        // TODO : I think this breaks the maifest file
        // window.history.replaceState({}, 'MorPhoto', '/')
      }
    }
    catch (e) {
      console.log('HERE IS AN ERROR', e)
      console.error(e)
    }
  }, [])

  if (!apolloDone) return <Loading message={APOLLOSTATES[apolloState]} />

  if (signedIn === null && !flags.useLoggedOut) return authScreen

  // These are the things the rest of the app needs to find:
  const appContext = {
    signOut,
    forceSignIn: () => {
      setUseOffline(false)
      forceSignIn()
    },
    onOfflineMenuClick,
    getPhotoUrls: async (photo, sizePriority) => {
      // console.log('getPhotoUrls::', { photo, size })
      try {
        if (photo.uploadToken) {
          const dbKeys = await imgStore.keys()
          if (dbKeys.indexOf(photo.uploadToken) > -1) {
            const data = await imgStore.getItem(photo.uploadToken)
            // console.log('getPhotoUrls:: Found DB Entry. using dataUrl', data)
            return [data.dataUrl]
          }
        }
        return sizePriority.map(sp => `${photo.paths.base}${photo.paths[sp]}`)
      }
      catch (e) {
        return null
      }
    },
    isPhone: isPhone(),
    isIos: isIos(),
    resetAllState,
    resetApp: () => {
      imgStore.clear()
      apolloClient.resetStore()
      stateStore.clear()
      resetAllState()
      signOut()
    },
    onAvatarClick,
    online,
    onSettingsClick,
    offlineSince,
    ...responsive,
    signedIn: Boolean(signedIn),
    role: !signedIn
      ? 'nonuser'
      : isGod
        ? 'admin'
        : 'user',
    currentUser: authData, // TODO: Need this supported by a graphql query
    onQRClick,
    addToHomeScreen
  }
  return (
  // We wrap the outside in an error handler
    <ThemeProvider theme={themes.home}>
      <ApolloProvider client={apolloClient}>
        <Query query={getUser} skip={!signedIn}>
          {({ loading, error, data }) => {
            if (loading) return <Loading message='UserData' />
            if (error) {
              console.error(error)
            }
            // If we've changed users then wipe the old user's stuff
            if (signedIn && authData.attributes.sub !== data.user.id) {
              console.log('USER CHANGE')
              imgStore.clear()
              apolloClient.resetStore()
              stateStore.clear()
              resetAllState()
            }
            else if (signedIn) appContext.user = data.user
            return (
              <AppContext.Provider value={appContext}>
                <IntlWrapper>
                  <Wrapped {...props}>
                    {installPrompt}
                  </Wrapped>
                </IntlWrapper>
              </AppContext.Provider>
            )
          }}
        </Query>
      </ApolloProvider>
    </ThemeProvider>
  )
}

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { domain } = state.home.current
  return { domain, flags: state.flags }
}

const mapDispatchToProps = dispatch => ({
  setHomeState: (newDomain, newParams) => {
    dispatch(actions.setHomeState(newDomain, newParams))
  },
  onCameraClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_CAMERA, { activeSeries: null }))
  },
  onLibraryClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.NEW_SERIES_LIBRARY, { activeSeries: null }))
  },
  onOfflineMenuClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.OFFLINE_DOWNLOAD))
  },
  onSettingsClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.SETTINGS))
  },
  resetAllState: () => {
    dispatch(actions.resetAllState())
  },
  dismissInstall: () => {
    dispatch(actions.setFlag('install', false))
  },
  setUseOffline: (bVal) => {
    dispatch(actions.setFlag('useLoggedOut', bVal))
  },
  onQRClick: () => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.QRCAPTURE))
  },
  onMenuBack: () => {
    dispatch(actions.homeStateBack())
  },
  onAvatarClick: (username) => {
    dispatch(actions.setHomeState(homeConfig.AppStateTypes.SEARCH, {
      search: { tab: homeConfig.SEARCH_TABS.USER, text: username }
    }))
  },
  onQRCapture: (type, activeSeries, initialPhoto) => {
    switch (type) {
      case 'series':
        dispatch(actions.setHomeState(homeConfig.AppStateTypes.SERIES, { activeSeries, initialPhoto }))
        break
      default:
        break
    }
  }
})

const withErrorBoundary = (Wrapped) => (props) => (
  <ErrorBoundary fallback={ErrorConnect}>
    <Wrapped {...props} />
  </ErrorBoundary>
)

/**
 * Nearly everything we do depends on who you are as a user so we commit this to state
 */

// The order we compose things here matters a lot
export default compose(
  // ORDER IS CRITICAL HERE
  withReduxProvider,
  connect(
    mapStateToProps,
    mapDispatchToProps
  ),
  withApolloAuthProvider,
  withErrorBoundary
)(App)
