// import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { allSeriesUpdate, feedUpdate, userSeriesUpdate } from './updateQueries'
import { deleteSeries } from '../mutations'
import { allSeries, userSeries, getMyFeed } from '../queries'
import { pushMutationPromise, popMutationPromise } from '../offline'

const withMutation = graphql(deleteSeries, {
  props: ({ mutate, ownProps }) => {
    return {
      deleteSeriesMutation: async () => {
        // When we're not creating a series the seriesId isn't passed in
        // so we add it now
        const variables = {
          id: ownProps.activeSeries
        }
        const optimisticResponse = {
          deleteSeries: ownProps.activeSeries
        }
        const mutationStackId = await pushMutationPromise({ variables, optimisticResponse }, deleteSeries)

        return mutate({
          variables,
          refetchQueries: [
            { query: allSeries, variables: { limit: 100 } },
            { query: userSeries },
            { query: getMyFeed }
          ],
          optimisticResponse,
          update: updateThunk()
        }).then(data => {
          // Here's where we take it out of the queue
          console.log('deleteSeriesMutation:: done', { data, mutationStackId })
          popMutationPromise(mutationStackId)
          return data
        })
      }
    }
  }
})

const updateThunk = (context, cb) => (store, { data }) => {
  // We need to write this query from scratch
  console.log('deleteSeriesMutation::updateThunk', { data })
  userSeriesUpdate(store, data.deleteSeries, true)
  allSeriesUpdate(store, { id: data.deleteSeries }, true)
  feedUpdate(store, { id: data.deleteSeries }, null, true)

  if (cb) cb(data.createSeries.series.id)
}

export default {
  withMutation,
  updateThunk
}
